.request_card_info_name {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
  margin-bottom: 0.2rem;
}
.request_card_container {
  width: 100%;
  min-height: 114px;
  background: #ffffff;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 6px;
  padding: 0.8rem;
  margin-bottom: 0.9rem;
  margin-top: 0.9rem;
}
.request_card_info {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 0.6rem;
}
.request_card_info_date {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: rgba(18, 18, 18, 0.6);
  margin: 0;
}
.request_card_utr {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
  margin: 0;
  margin-top: 0.7rem;
}
.request_card_info_image {
  width: 52px;
  height: 52px;
  border-radius: 4px;
}
.fee_request_detail {
  margin-top: 1.5rem;
}
.fee_request_detail_name {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
}
.fee_request_detail_amount {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
}
.fee_request_detail_date {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: rgba(18, 18, 18, 0.6);
}

.reason_for_rejection {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
  margin-bottom: 0.5rem;
}

.receipt_filter_icon {
  cursor: pointer;
}
.receipt_container_title {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1.5rem;
}

.payment_acknowledge_btn,
.payment_acknowledge_btn:focus-visible,
.payment_acknowledge_btn:focus {
  width: 100%;
  height: 47px;
  background: #ffffff;
  box-shadow: 0px 4px 4px #002bbe;
  border-radius: 6px;
  border: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: rgba(18, 18, 18, 0.9);
  outline: transparent;
  padding: 15px 33px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-block: 1rem;
}
